'use strict';

'use strict';

/**
 * @alias to pluck
 */
export {default as pluck} from './pluck';

/**
 * Returns the size of an Object
 * @param     {Object}    obj
 * @return    {Number}    size
 */
export function objectSize(obj) {
  let size = 0;
  let key;

    for (key in obj) {
      if (obj.hasOwnProperty(key)) {
        size++;
      }
    }

  return size;
}

/**
 * Checks if value is the language type of Object
 * will skip values === null
 * @param     {Mixed}    prop
 * @return    {Bool}
 */
export function isObject(prop) {
  // Weird behaviour with spec in Javascript, the typeof Array is Object
  return prop !== null && !Array.isArray(prop) && typeof prop === 'object';
}

/**
 * Checks if value is classified as a Function object
 * @param     {Mixed}    prop
 * @return    {Bool}
 */
export function isFunction(prop) {
  return typeof prop === 'function';
}

/**
 * Checks if value is classified as a String primitive or object
 * @param     {Mixed}    prop
 * @return    {Bool}
 */
export function isString(prop) {
  return typeof prop === 'string';
}

/**
 * Checks if value is classified as an Array object
 * @param     {Mixed}    prop
 * @return    {Bool}
 */
export function isArray(prop) {
  return Array.isArray(prop);
}

/**
 * Checks if value is undefined
 * @param     {Mixed}    prop
 * @return    {Bool}
 */
export function isUndefined(prop) {
 return typeof prop === 'undefined';
}

/**
 * Bind to itself
 * @param     {args...}
 * @return    {Undefined}
 * @example   bind(this, 'handleClick', 'handleSubmit');
 */
export function bind() {
  let args = Array.prototype.slice.call(arguments);
  let ctx = args.splice(0, 1)[0];
  for (let i = 0; i < args.length; i++) {
    ctx[args[i]] = ctx[args[i]].bind(ctx);
  }
}

/**
 * Binds methods of an object to the object itself,
 * overwriting the existing method.
 * Method names may be specified as individual arguments
 * or as arrays of method names
 * @param     {Object}  obj
 * @return    {Object}  result
 */
export function bindAll(obj) {
  let result = obj;

  for (let prop in result) {
    if (result.hasOwnProperty(prop) && typeof result[prop] === 'function') {
      result[prop] = result[prop].bind(result);
    }
  }
  return result;
}

/**
 * This method is like _.assign except that it
 * recursively merges own and inherited enumerable
 * string keyed properties of source objects into the
 * destination object
 * @param     {Object}      target
 * @param     {Object}      source
 * @return    {Object}      target
 */
export function mergeDeep(target, source) {
  if (isObject(target) && isObject(source)) {
    for (const key in source) {
      if (isObject(source[key])) {
        if (!target[key]) {
          Object.assign(target, { [key]: {} });
        }
        mergeDeep(target[key], source[key]);
      } else {
        target[key] = source[key];
      }
    }
  }
  return target;
}

/**
 * Recursive object copy
 *
 * @param     {Array<Objects> || Objects}    obj
 * @return    {Array<Objects> || Objects}
 */
/* eslint-disable */
export function deepCopy(obj) {
  var result;

  // Handle Date
  if (obj instanceof Date) {
    result = new Date();
    result.setTime(obj.getTime());
    return result;
  }

  var gdcc = '__getDeepCircularCopy__';
  if (obj !== Object(obj)) {
      return obj; // primitive value
  }

  var set = gdcc in obj;
  var cache = obj[gdcc];

  if (set && typeof cache === 'function') {
      return cache();
  }
  // else
  obj[gdcc] = function() {
    return result;
  }; // overwrite

  // Array of objects
  if (obj instanceof Array) {
    result = [];
    for (var i=0; i<obj.length; i++) {
        result[i] = deepCopy(obj[i]);
    }
  } else if(obj instanceof Object) {
    // Object or nested objects (tree)
    result = {};
    for (var prop in obj) {
      if (prop !== gdcc) {
        result[prop] = deepCopy(obj[prop]);
      } else if (set) {
        result[prop] = deepCopy(cache);
      }
    }
  } else {
    throw new Error('Unable to copy obj! Its type is not supported.');
  }

  if (set) {
      obj[gdcc] = cache; // reset
  } else {
      delete obj[gdcc]; // unset again
  }
  return result;
}
/*eslint-enable */

/**
 * Detect IE browser
 * @param  {String} prop
 * @return {Bool}
 */
export function isIEBrowser(prop) {
  switch (prop) {
    case 'ie10':
      return navigator.appVersion.indexOf('MSIE 10') !== -1 && navigator.appVersion.indexOf('Trident') === -1;
    case 'ie11':
      return navigator.appVersion.indexOf('Trident') !== -1 && navigator.appVersion.indexOf('MSIE 10') === -1;
    case 'edge':
      return navigator.appVersion.indexOf('Edge') !== -1;
    // IE10 || IE11
    default:
      return navigator.appVersion.indexOf('Trident') !== -1 || navigator.appVersion.indexOf('MSIE 10') !== -1;
  }
}

 /**
  * Detect IE browser version
  * @return {String}
  */
export function detectIEVersion() {
  let ua = window ? window.navigator.userAgent : navigator.userAgent;

  // Test values; Uncomment to check result 

  // IE 10
  // ua = 'Mozilla/5.0 (compatible; MSIE 10.0; Windows NT 6.2; Trident/6.0)';

  // IE 11
  // ua = 'Mozilla/5.0 (Windows NT 6.3; Trident/7.0; rv:11.0) like Gecko';

  // Edge 12 (Spartan)
  // ua = 'Mozilla/5.0 (Windows NT 10.0; WOW64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/39.0.2171.71 Safari/537.36 Edge/12.0';

  // Edge 13
  // ua = 'Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/46.0.2486.0 Safari/537.36 Edge/13.10586';

  let msie = ua.indexOf('MSIE ');
  if (msie > 0) {
    // IE 10 or older => return version number
    return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
  }

  let trident = ua.indexOf('Trident/');
  if (trident > 0) {
    // IE 11 => return version number
    let rv = ua.indexOf('rv:');
    return parseInt(ua.substring(rv + 3, ua.indexOf('.', rv)), 10);
  }

  let edge = ua.indexOf('Edge/');
  if (edge > 0) {
    // Edge (IE 12+) => return version number
    return parseInt(ua.substring(edge + 5, ua.indexOf('.', edge)), 10);
  }

  // other browser
  return false;
}

/**
 * Shallow comparison between 2 arrays
 * @param  {Array} arr1
 * @param  {Array} arr2
 * @param  {Function} cb {optional}
 * @return {Bool}
 */
export function isEqualArray(arr1, arr2, cb) {
  // return false if both are not an array
  if (!Array.isArray(arr1) || !Array.isArray(arr2) || arr1.length !== arr2.length) {
    return false;
  }

  if (typeof cb !== 'function') {
    cb = (element, index) => element === arr2[index];
  }

  return arr1.every(cb);
}

/**
 * From Underscore.js:
 * Returns a function, that, when invoked, will only be triggered at most once
 * during a given window of time. Normally, the throttled function will run as
 * much as it can, without ever going more than once per wait duration; but if
 * youd like to disable the execution on the leading edge, pass {leading: false}.
 * To disable execution on the trailing edge, ditto.
 * @param {Function} func
 * @param {Number} wait     milliseconds
 * @param {Object} options
 *
 * @return {Function}
 */
export function throttle(func, wait, options) {
  let context;
  let args;
  let result;
  let timeout = null;
  let previous = 0;
  if (!options) {
    options = {};
  }
  let later = () => {
    previous = options.leading === false ? 0 : Date.now();
    timeout = null;
    result = func.apply(context, args);
    if (!timeout) {
      context = args = null;
    }
  };
  return function() {
    let now = Date.now();
    if (!previous && options.leading === false) {
      previous = now;
    }
    let remaining = wait - (now - previous);
    context = this;
    args = arguments;
    if (remaining <= 0 || remaining > wait) {
      if (timeout) {
        clearTimeout(timeout);
        timeout = null;
      }
      previous = now;
      result = func.apply(context, args);
      if (!timeout) {
        context = args = null;
      }
    } else if (!timeout && options.trailing !== false) {
      timeout = setTimeout(later, remaining);
    }
    return result;
  };
}

/**
 * Polyfill for finding document scrollTop.
 */
export function getScrollTop() {
  let scrollTop = window.pageYOffset;
  if (typeof scrollTop === 'undefined') {
    scrollTop = document.documentElement.scrollTop || document.body.scrollTop;
  }
  if (isNaN(scrollTop) || typeof scrollTop === 'undefined') {
    throw new Error('Unable to retrieve scrollTop. :(');
  }
  return scrollTop;
}
